.darkBG {
    background-color: rgba(0, 0, 0, 0.2);
    width: 100vw;
    height: 100vh;
    z-index: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
}

.centered {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.modal {
    width: 550px;
    height: 400px;
    background: white;
    color: white;
    z-index: 10;
    border-radius: 16px;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.04);
}

.modalHeader {
    height: 50px;
    background: rgb(217, 224, 248);
    overflow: hidden;
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
}

.heading {
    margin: 0;
    padding: 10px;
    color: #2c3e50;
    font-weight: 500;
    font-size: 18px;
    text-align: center;
}

.modalContent {
    padding: 10px;
    font-size: 14px;
    color: #2c3e50;
    text-align: center;
}

.modalActions {
    position: absolute;
    bottom: 2px;
    margin-bottom: 10px;
    width: 100%;
    /* border: 2px solid blue; */
}

.actionsContainer {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.label-container {
    display: flex;
    width: 0px;
    white-space: nowrap; /* Prevent text wrapping */
    overflow: hidden; /* Hide overflowing text */
    text-overflow: ellipsis; /* Display an ellipsis (...) when text overflows */
    margin-top: 10px;
  }

.closeBtn {
    cursor: pointer;
    font-weight: 500;
    padding: 4px 8px;
    /* border-radius: 8px; */
    border: none;
    font-size: 18px;
    color: #2c3e50;
    /* background: white; */
    transition: all 0.25s ease;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.06);
    position: absolute;
    right: 0;
    top: 0;
    align-self: flex-end;
    margin-top: -7px;
    margin-right: -7px;
}

.closeBtn:hover {
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.04);
    border-radius: 20px;
    background-color: #fff;
    /* transform: translate(-4px, 4px); */
}

.deleteBtn {
    margin-top: 10px;
    cursor: pointer;
    font-weight: 500;
    padding: 11px 28px;
    border-radius: 12px;
    font-size: 0.8rem;
    border: none;
    color: #fff;
    background: #ff3e4e;
    transition: all 0.25s ease;
}

.deleteBtn:hover {
    box-shadow: 0 10px 20px -10px rgba(255, 62, 78, 0.6);
    /* transform: translateY(-5px); */
    background: #61171d;
}

.postBtn {
    margin-top: 10px;
    cursor: pointer;
    font-weight: 500;
    padding: 11px 28px;
    border-radius: 12px;
    font-size: 1rem;
    border: none;
    color: #ffffff;
    background: #0e6a25;
    transition: all 0.25s ease;
}

.postBtn:hover {
    box-shadow: none;
    transform: none;
    background: rgb(18, 78, 27);
}

.primaryBtn {
    margin: 20px 10px;
    cursor: pointer;
    font-weight: 500;
    padding: 13px 25px;
    border-radius: 15px;
    font-size: 1rem;
    border: none;
    color: white;
    background: #185adb;
    transition: all 0.25s ease;
}

.primaryBtn:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px -10px rgba(24, 90, 219, 0.6);
}

.textarea {
    width: 100%;
    height: 100%;
    background-color: antiquewhite;
    margin-bottom: 10px;
    resize: none;
    border-radius: 5px;
    font-size: 20px;
    padding: 10px;
}

.imagename {
    background-color: #abd3f6a0;
    width: 530px;
    height: 40px;
    display: flex;
    margin-left: 10px;
    border-radius: 5px;
    /* margin-right: -10px; */
}